import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { Howl } from "howler";
import Head from "next/head";
import Link from "next/link";
import { Line } from "react-konva";
import { Stage, Layer } from "react-konva";

export default function RoseDay() {
  const [showMessage, setShowMessage] = useState(false);
  const [audio, setAudio] = useState(null);
  const [rosePath, setRosePath] = useState([]);
  const [progress, setProgress] = useState(0);

  useEffect(() => {
    const sound = new Howl({ src: ["/romantic-music.mp3"], autoplay: true, loop: true, volume: 0.5 });
    setAudio(sound);
    return () => sound.stop();
  }, []);

  useEffect(() => {
    setTimeout(() => setShowMessage(true), 5000);
  }, []);

  useEffect(() => {
    const path = [
      { x: 150, y: 300 }, { x: 140, y: 250 }, { x: 130, y: 200 },
      { x: 120, y: 150 }, { x: 110, y: 120 }, { x: 130, y: 100 },
      { x: 150, y: 80 }, { x: 170, y: 100 }, { x: 190, y: 120 },
      { x: 180, y: 150 }, { x: 170, y: 200 }, { x: 160, y: 250 },
      { x: 150, y: 300 }
    ];
    setRosePath(path);
    let step = 0;
    const interval = setInterval(() => {
      step++;
      setProgress(step);
      if (step >= path.length) clearInterval(interval);
    }, 300);
  }, []);

  return (
    <div className="relative min-h-screen bg-gradient-to-b from-black via-red-900 to-black text-white flex items-center justify-center">
      <Head>
        <title>Happy Rose Day</title>
      </Head>
      <nav className="absolute top-4 left-4">
        <Link href="/" legacyBehavior>
          <a className="text-pink-300 hover:text-pink-500 text-lg font-bold">Home</a>
        </Link>
      </nav>
      <div className="relative w-[300px] h-[400px]">
        <Stage width={300} height={400} className="absolute">
          <Layer>
            <Line
              points={rosePath.slice(0, progress).flatMap(p => [p.x, p.y])}
              stroke="red"
              strokeWidth={4}
              lineCap="round"
              lineJoin="round"
            />
          </Layer>
        </Stage>
      </div>
      {showMessage && (
        <motion.h1
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 2 }}
          className="absolute bottom-10 text-4xl font-extrabold text-pink-400 drop-shadow-lg"
        >
          Happy Rose Day, My Love! ðŸŒ¹
        </motion.h1>
      )}
    </div>
  );
}
